.form {
  @media (min-width: $tablet-width) {
    padding-top: 0;
    border: 0;
  }
}

.form__title {
  margin-bottom: 37px;
  color: $secondary-color;
  font-family: $secondary-font;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;

  @media (min-width: $tablet-width) {
    margin-bottom: 57px;
    padding-top: 20px;
    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 30px;
  }
}

.form__description {
  margin-bottom: 32px;
  color: $secondary-color;
  font-family: $secondary-font;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin-bottom: 60px;
    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 70px;
  }
}

.form__section {
  display: grid;
  margin-bottom: 32px;

  @media (min-width: $tablet-width) {
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 80px;
  }
}

.form__section-realive-wrapper {
  @media (min-width: $desktop-width) {
    display: grid;
    align-items: flex-start;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    width: $desktop-width;
    margin: 0 auto;
    margin-bottom: 2px;
    padding-left: 110px;
    padding-right: 110px;
  }
}

.form__cat-info {
  display: grid;
  gap: 14px;
  margin-bottom: 10px;
  border: 0;
  font-family: $secondary-font;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin-bottom: 60px;
    gap: 22px;
    font-size: 20px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    width: auto;
    gap: 23px;
    padding: 0;
    margin: 0;
  }
}

.form__cat-info-label {
  display: flex;
  flex-wrap: wrap;
  column-gap: 13px;
  align-items: center;
}

.form__cat-info-input {
  width: 174px;
  padding: 12px 15px;
  flex-grow: 1;
  outline: 1px solid #e7e7e7;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  text-transform: inherit;
  appearance: textfield;

  &[type="number"]::-webkit-outer-spin-button,
  &[type="number"]::-webkit-inner-spin-button {
    appearance: none;
  }

  &[type="number"],
  &[type="number"]:hover,
  &[type="number"]:focus {
    appearance: textfield;
  }

  @media (min-width: $tablet-width) {
    padding: 11px 19px;
  }
}

.form__cat-info-input:hover {
  outline: 1px solid #cdcdcd;
}

.form__cat-info-input:focus {
  outline: 2px solid #444444;
}

.form__programm-type {
  padding: 0;
  margin: 0;
  margin-bottom: 25px;
  font-family: $secondary-font;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  border: 0;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-width) {
    margin-bottom: 65px;
    border: 0;
    font-size: 20px;
    line-height: 24px;
  }
}

.form__programm-type-wrapper {
  @media (min-width: $desktop-width) {
    width: auto;
    padding: 0;
    margin: 0;
  }
}

.form__programm-type-inner-wrapper {
  display: grid;
  row-gap: 13px;
  padding-top: 22px;
  padding-bottom: 19px;

  @media (min-width: $tablet-width) {
    row-gap: 12px;
    padding: 23px 23px 27px 27px;
    border: 1px solid #d9d9d9;
  }
}

.form__programm-type-label {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 15px;

  @media (min-width: $tablet-width) {
    gap: 20px;
  }
}

.form__programm-type-input {
  width: 30px;
  height: 30px;
  position: relative;
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  appearance: none;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}


.form__programm-type-input:checked::before {
  content: "";
  position: absolute;
  width: 13px;
  height: 13px;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
  background-color: $bg-color;

  @media (min-width: $tablet-width) {
    width: 18px;
    height: 18px;
  }
}


.form__programm-type-input::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.form__programm-type-text:hover {
  cursor: pointer;
}

.form__programm-type-label:hover .form__programm-type-input::after,
.form__programm-type-input:focus::after {
  outline: 1px solid #bababa;
  border-radius: 50%;
}

.form__programm-type-input:disabled,
.form__programm-type-input:disabled ~ .form__programm-type-text {
  opacity: 0.3;
}

.form__legend {
  position: relative;
  width: 100%;
  padding: 0;
}

.form__legend::after {
  content: "";
  position: absolute;
  top: 17px;
  height: 2px;
  width: 100%;
  background-color: $bg-color;
  z-index: -1;

  @media (min-width: $tablet-width) {
    top: 20px;
  }
}

.form__legend-title {
  margin-bottom: 36px;
  padding-left: 0;
  font-family: $secondary-font;
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  text-transform: none;
  color: $secondary-color;

  @media (min-width: $tablet-width) {
    margin-bottom: 77px;
    padding-left: 30px;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 70px;
  }
}

.form__legend-text {
  display: inline-block;
  padding: 0 20px;
  background-color: #ffffff;

  @media (min-width: $tablet-width) {
    padding: 0 40px;
  }
}

.form__legend-text br {
  @media (min-width: $tablet-width) {
    display: none;
  }
}

.form__owner-contacts {
  margin: 0;
  margin-bottom: 23px;
  padding: 0;
  border: 0;
  font-family: $secondary-font;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin-bottom: 65px;
    font-size: 20px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 70px;
  }
}

.form__owner-wrapper {
  display: grid;
  row-gap: 14px;

  @media (min-width: $tablet-width) {
    row-gap: 20px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 1fr 1fr;
    column-gap: 81px;
  }
}

.form__owner-contacts-label {
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 13px;
}

.form__owner-contacts-input {
  position: relative;
  padding: 13px 40px 12px 15px;
  padding-right: 40px;
  flex-grow: 1;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  text-transform: inherit;
  color: inherit;
  background-repeat: no-repeat;
  background-position: center right 12px;

  @media (min-width: $tablet-width) {
    padding: 13px 25px;
    padding-right: 40px;
    background-position: center right 14px;
  }
}

.form__owner-contacts-icon {
  position: absolute;
  right: 13px;
}

.form__owner-contacts-input:invalid {
  padding: 12px 39px 11px 14px;
  border: 2px solid #ff8282;
}

.form__owner-contacts-input:invalid + .form__owner-contacts-icon path {
  stroke: #ff8282;
}

.form__owner-contacts-input:invalid:placeholder-shown {
  padding: 13px 40px 12px 15px;
  border: 1px solid #e7e7e7;

  @media (min-width: $tablet-width) {
    padding-left: 20px;
  }
}

.form__owner-contacts-input:invalid:placeholder-shown + .form__owner-contacts-icon path {
  stroke: #000000;
}

.form__owner-contacts-input:invalid:placeholder-shown:hover,
.form__owner-contacts-input:hover {
  border-color: #cdcdcd;
}

.form__owner-contacts-input:invalid:placeholder-shown:focus,
.form__owner-contacts-input:focus {
  padding: 12px 39px 11px 14px;
  border: 2px solid #444444;
  outline: none;

  @media (min-width: $tablet-width) {
    padding-left: 20px;
  }
}

.form__owner-contacts-input:focus + .form__owner-contacts-icon path {
  stroke: $bg-color;
}

.form__owner-contacts-input + .form__owner-contacts-icon path {
  fill: none;
  stroke: #000000;
}

.form__comments {
  margin: 0;
  margin-bottom: 23px;
  padding: 0;
  border: 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 62px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 70px;
  }
}

.form__comments .form__legend {
  margin-bottom: 2px;

  @media (min-width: $tablet-width) {
    margin-bottom: 11px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.form__comments-textarea {
  width: 100%;
  max-height: 150px;
  padding: 14px;
  font-family: $secondary-font;
  color: $main-color;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  border: 1px solid #e7e7e7;

  @media (min-width: $tablet-width) {
    max-height: 176px;
    font-size: 20px;
    line-height: 30px;
    padding: 11px 20px;
  }
}

.form__comments-textarea:hover {
  outline: 1px solid #cdcdcd;
}

.form__comments-textarea:focus {
  outline: 2px solid #444444;
}

.form__additionally {
  margin: 0;
  margin-bottom: 36px;
  padding: 0;
  display: grid;
  gap: 14px;
  font-family: $secondary-font;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  border: 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 52px;
    font-size: 20px;
    line-height: 24px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 60px;
  }
}

.form__additionally .form__legend-title {
  @media (min-width: $tablet-width) {
    margin-bottom: 56px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 53px;
  }
}

.form__additionally-wrapper {
  display: grid;
  row-gap: 11px;

  @media (min-width: $tablet-width) {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 32px;
    column-gap: 60px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}

.form__additionally-label {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 15px;

  @media (min-width: $tablet-width) {
    column-gap: 20px;
  }
}

.form__additionally-input {
  width: 30px;
  height: 30px;
  position: relative;
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  appearance: none;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.form__additionally-text:hover {
  cursor: pointer;
}

.form__additionally-label:hover .form__additionally-input::after,
.form__additionally-input:focus::after {
  outline: 1px solid #bababa;
}

.form__additionally-input:disabled,
.form__additionally-input:disabled ~ .form__additionally-text {
  opacity: 0.3;
}

.form__additionally-input:checked::before {
  content: "";
  position: absolute;
  width: 18px;
  height: 14px;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-image: url("../img/sprite.svg#form-additionally-icon");

  @media (min-width: $tablet-width) {
    width: 24px;
    height: 19px;
  }
}

.form__additionally-input::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.form__footer {
  display: grid;
  row-gap: 21px;

  @media (min-width: $tablet-width) {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 62px;
    align-items: center;
  }

  @media (min-width: $desktop-width) {
    column-gap: 0;
  }
}

.form__submit-button {
  margin: 0 auto;
  display: block;
  border: 0;
  min-width: 280px;
  padding: 10px;

  @media (min-width: $tablet-width) {
    margin: 0;
    margin-right: auto;
    min-width: 284px;
    padding: 14px 10px;
    font-size: 20px;
    line-height: 26px;
  }

  @media (min-width: $desktop-width) {
    min-width: 570px;
    margin-right: 80px;
    padding-top: 13px;
  }
}

.form__note {
  margin: 0;
  text-align: center;
  font-size: 14px;
  line-height: 16px;
  color: $secondary-color;

  @media (min-width: $tablet-width) {
    margin-bottom: 6px;
    font-size: 16px;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 1px;
  }
}
